  );
  border: 1px solid rgba(100, 130, 170, 0.35);
  border-radius: 14px;
  cursor: pointer;
  transition: all 0.15s ease;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
  box-shadow:
    0 1px 4px rgba(0, 0, 0, 0.35),
    0 0 6px rgba(80, 120, 180, 0.12),
    inset 0 1px 0 rgba(255, 255, 255, 0.08);
  white-space: nowrap;
  position: relative;
  /* Explicitly prevent dropdown behavior */
  z-index: auto;
}

/* Ensure pills are NOT dropdowns */
body.page-active-picks .ft-pill::after {
  display: none !important;
  content: none !important;
}

body.page-active-picks .ft-pill:hover {
