body.page-active-picks .ft-dropdown-btn:hover,
body.page-active-picks .ft-dropdown-btn.open {
  color: #fff;
  border-color: rgba(255, 255, 255, 0.25);
}

body.page-active-picks .ft-dropdown-menu {
