/* ===== FILTER CHIPS WRAPPER ===== */
body.page-active-picks .table-filter-chips-wrapper {
  position: relative;
  margin-bottom: 12px;
  z-index: 101;
  min-height: 0;
  transition: min-height 0.3s ease;
}

body.page-active-picks
  .table-filter-chips-wrapper:has([data-has-chips="true"]) {
  min-height: 60px;
}

/* ===== FILTER CHIPS ===== */
body.page-active-picks .table-filter-chips {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  padding: 16px 20px;
  min-height: 62px;
  background: rgba(6, 12, 28, 0.92);
  border: 1px solid rgba(0, 214, 137, 0.25);
  border-radius: 14px;
  box-shadow:
    0 10px 28px rgba(0, 0, 0, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.06);
  -webkit-backdrop-filter: blur(16px);
  backdrop-filter: blur(16px);
  animation: filterChipsPopIn 0.35s cubic-bezier(0.34, 1.56, 0.64, 1);
  transform-origin: top center;
  position: relative;
}

body.page-active-picks .table-filter-chips[data-has-chips="false"] {
  display: none !important;
}

@keyframes filterChipsPopIn {
  from {
    opacity: 0;
    transform: translateY(-10px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

body.page-active-picks .filter-chip {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.875rem;
  background: linear-gradient(
    135deg,
    rgba(0, 214, 137, 0.2) 0%,
