  animation: chipSlideIn 0.25s ease-out;
}

@keyframes chipSlideIn {
  from {
    opacity: 0;
    transform: translateX(-8px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

body.page-active-picks .filter-chip .chip-remove {
  cursor: pointer;
  padding: 0 4px;
  font-size: 1rem;
