    border-color: rgba(0, 255, 170, 0.5);
    box-shadow:
        inset 0 1px 3px rgba(0, 255, 170, 0.2),
        0 0 0 1px rgba(0, 255, 170, 0.3),
        0 2px 8px rgba(0, 214, 137, 0.2);
