}

/* Focus state for accessibility - minimal */
.date-toggle-btn:focus {
    outline: none;
}

.date-toggle-btn:focus-visible {
    outline: 1px solid rgba(0, 214, 137, 0.5);
    outline-offset: 2px;
}

/* Custom Date Range - Ultra-floating minimal popup */
.custom-date-range {
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
    margin-top: 6px; /* Tighter margin */
