/* Metallic Emerald calendar */
.custom-calendar {
    margin-top: 4px;
    padding: 6px 6px 4px;
    border-radius: 10px;
    background: radial-gradient(circle at 0% 0%, rgba(0, 214, 137, 0.3), transparent 55%),
