}

.calendar-nav:focus-visible {
    outline: 1px solid rgba(0, 214, 137, 0.9);
    outline-offset: 2px;
}

.custom-calendar-grid {
    display: grid;
    grid-template-rows: auto 1fr;
    gap: 2px;
}

.calendar-weekdays {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 1px;
    font-family: var(--font-body);
    font-size: 0.46rem;
