    cursor: default;
}

.calendar-day:not(.empty):hover {
    background: linear-gradient(135deg, rgba(0, 214, 137, 0.32), rgba(18, 194, 255, 0.24));
    color: rgba(5, 12, 10, 0.98);
    box-shadow: 0 0 8px rgba(0, 214, 137, 0.45);
