/* =========================================================
   ULTRA-MINIMALISTIC CALENDAR PICKER STYLING
   Sleek, floating calendar tiles for date inputs
   ========================================================= */

/* Enhanced calendar picker indicator - ultra-minimal */
input[type="date"]::-webkit-calendar-picker-indicator {
    filter: brightness(0) saturate(100%) invert(54%) sepia(98%) saturate(450%) hue-rotate(130deg) brightness(95%) contrast(101%);
    cursor: pointer;
    opacity: 0.6; /* Subtle opacity for minimalism */
    transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    transform: scale(0.85); /* Smaller, sleeker icon */
}

input[type="date"]::-webkit-calendar-picker-indicator:hover {
    opacity: 0.9; /* Enhanced on hover */
    transform: scale(0.9); /* Subtle scale effect */
    filter: brightness(0) saturate(100%) invert(54%) sepia(98%) saturate(450%) hue-rotate(130deg) brightness(110%) contrast(105%);
}

input[type="date"]::-webkit-calendar-picker-indicator:active {
    transform: scale(0.8); /* Tighter press effect */
}

/* Enhanced date input styling for calendar tiles */
.date-input {
    background: rgba(15, 30, 45, 0.4); /* More transparent for floating effect */
    border: 1px solid rgba(0, 214, 137, 0.08); /* Ultra-subtle border */
    color: var(--color-text-primary, rgba(255, 255, 255, 0.9));
    font-family: 'Cormorant Garamond', Georgia, serif;
    font-size: 0.55rem; /* Tighter font size */
    font-weight: 500;
    padding: 4px 6px; /* Tighter padding */
    border-radius: 8px; /* Sleeker radius */
    transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    width: 100%;
    cursor: pointer;
    -webkit-backdrop-filter: blur(8px); /* Subtle blur for floating effect */
    backdrop-filter: blur(8px);
}

.date-input:hover {
    border-color: rgba(0, 214, 137, 0.2); /* Subtle emerald tint */
    background: rgba(15, 30, 45, 0.6); /* Slightly more opaque on hover */
    box-shadow: 0 0 8px rgba(0, 214, 137, 0.08); /* Ultra-subtle glow */
}

.date-input:focus {
    outline: none;
    border-color: rgba(0, 214, 137, 0.3); /* Enhanced emerald border */
    box-shadow: 0 0 0 1px rgba(0, 214, 137, 0.1),
                0 0 12px rgba(0, 214, 137, 0.06); /* Subtle focus ring */
    background: rgba(15, 30, 45, 0.7); /* More opaque when focused */
}

/* Force dark theme for calendar popup - ultra-minimal tiles */
input[type="date"] {
    color-scheme: dark;
}

/* Enhanced calendar popup styling for Chromium browsers - sleeker tiles */
input[type="date"]::-webkit-datetime-edit {
    color: var(--color-text-primary, rgba(255, 255, 255, 0.9));
    font-weight: 500;
    letter-spacing: 0.02em; /* Tighter letter spacing */
}

input[type="date"]::-webkit-datetime-edit-fields-wrapper {
    background: transparent;
    padding: 1px 0; /* Minimal padding */
}

/* Calendar tile hover states - ultra-subtle */
input[type="date"]:hover::-webkit-calendar-picker-indicator {
    opacity: 0.8;
    transform: scale(0.88);
}

/* Focus state for accessibility - minimal ring */
input[type="date"]:focus::-webkit-calendar-picker-indicator {
    opacity: 1;
    transform: scale(0.92);
}

/* =========================================================
   DATE RANGE SELECTOR - Compact Dropdown Toggle
   Single button with dropdown for date range selection
   ========================================================= */

.date-range-selector {
    position: relative;
    display: inline-flex;
    align-items: flex-end; /* Align button to bottom */
    margin: 0;
    padding: 0;
    z-index: 500;
}

/* Main toggle button */
.date-range-toggle {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    margin: 0; /* Reset browser default */
    padding: 6px 12px;
    background: rgba(10, 22, 38, 0.8);
    border: 1px solid rgba(0, 214, 137, 0.4);
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.15s ease;
    font-family: 'Cormorant Garamond', Georgia, serif;
}

.date-range-toggle:hover {
    background: rgba(0, 214, 137, 0.15);
    border-color: rgba(0, 214, 137, 0.6);
}

.date-range-toggle[aria-expanded="true"] {
    background: rgba(0, 214, 137, 0.2);
    border-color: rgba(0, 214, 137, 0.7);
}

.date-range-label {
    font-size: 0.75rem;
    font-weight: 600;
    color: #00ffaa;
    text-transform: uppercase;
    letter-spacing: 0.08em;
}

.date-range-arrow {
    font-size: 0.55rem;
    color: rgba(0, 214, 137, 0.7);
    transition: transform 0.15s ease;
}

.date-range-toggle[aria-expanded="true"] .date-range-arrow {
    transform: rotate(180deg);
}

/* Dropdown panel */
.date-range-dropdown {
    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    min-width: 120px;
    background: rgba(8, 18, 30, 0.98);
    border: 1px solid rgba(0, 214, 137, 0.5);
    border-radius: 6px;
    padding: 4px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5), 0 0 12px rgba(0, 214, 137, 0.15);
    z-index: 1000;
}

.date-range-dropdown[hidden] {
    display: none;
}

/* Dropdown options */
.date-range-option {
    display: block;
    width: 100%;
    padding: 5px 10px;
    background: transparent;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    text-align: left;
    font-family: 'Cormorant Garamond', Georgia, serif;
    font-size: 0.7rem;
    font-weight: 500;
    color: rgba(230, 242, 246, 0.85);
    text-transform: uppercase;
    letter-spacing: 0.05em;
    transition: all 0.1s ease;
}

.date-range-option:hover {
    background: rgba(0, 214, 137, 0.15);
    color: #00ffaa;
}

.date-range-option.active {
    background: rgba(0, 214, 137, 0.25);
    color: #00ffaa;
    font-weight: 700;
}

/* Divider between date range groups */
.date-range-divider {
    height: 1px;
    background: rgba(0, 214, 137, 0.2);
    margin: 4px 0;
    border: none;
}

/* =========================================================
   CUSTOM CALENDAR CARD - Stylish Dropdown
   Elegant calendar picker card for custom date ranges
   ========================================================= */

.custom-calendar-card {
    position: absolute;
    top: calc(100% + 8px);
    left: 0;
    min-width: 280px;
    max-width: 90vw;
    background: linear-gradient(140deg, rgba(3, 16, 12, 0.98), rgba(8, 30, 22, 0.96));
    border: 1px solid rgba(0, 214, 137, 0.4);
    border-radius: 12px;
    padding: 0;
    box-shadow: 
        0 8px 32px rgba(0, 0, 0, 0.8),
        0 0 16px rgba(0, 214, 137, 0.2),
        inset 0 1px 0 rgba(255, 255, 255, 0.1);
    z-index: 1001;
    overflow: hidden;
    -webkit-backdrop-filter: blur(24px);
    backdrop-filter: blur(24px);
}

.custom-calendar-card::before {
    content: "";
    position: absolute;
    inset: 0;
    pointer-events: none;
    background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.08) 50%, transparent 70%);
    opacity: 0.4;
    mix-blend-mode: screen;
}

.custom-calendar-card[hidden] {
    display: none;
}

/* Calendar Card Header */
.calendar-card-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 12px;
    border-bottom: 1px solid rgba(0, 214, 137, 0.2);
    background: linear-gradient(135deg, rgba(0, 214, 137, 0.1), rgba(18, 194, 255, 0.05));
}

.calendar-card-title {
    font-family: 'Cormorant Garamond', Georgia, serif;
    font-size: 0.75rem;
    font-weight: 700;
    color: #00ffaa;
    text-transform: uppercase;
    letter-spacing: 0.08em;
    text-shadow: 0 0 6px rgba(0, 255, 170, 0.4);
}

.calendar-card-close {
    width: 20px;
    height: 20px;
    border: none;
    background: rgba(0, 214, 137, 0.15);
    border-radius: 50%;
    color: rgba(0, 255, 170, 0.8);
    font-size: 1rem;
    line-height: 1;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
    padding: 0;
}

.calendar-card-close:hover {
    background: rgba(0, 214, 137, 0.3);
    color: #00ffaa;
    transform: scale(1.1);
    box-shadow: 0 0 8px rgba(0, 214, 137, 0.4);
}

/* Calendar Card Body */
.calendar-card-body {
    padding: 12px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

/* Date Target Buttons */
.calendar-date-targets {
    display: flex;
    gap: 6px;
    margin-bottom: 0;
}

.date-target-btn {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 6px 8px;
    border-radius: 8px;
    border: 1px solid rgba(0, 214, 137, 0.25);
    background: rgba(3, 18, 15, 0.6);
    cursor: pointer;
    transition: all 0.2s ease;
    gap: 2px;
}

.date-target-btn:hover {
    background: rgba(0, 214, 137, 0.1);
    border-color: rgba(0, 214, 137, 0.4);
}

.date-target-btn.active {
    background: linear-gradient(135deg, rgba(0, 214, 137, 0.3), rgba(18, 194, 255, 0.2));
    border-color: rgba(0, 214, 137, 0.6);
    box-shadow: 0 0 12px rgba(0, 214, 137, 0.3);
}

.date-target-label {
    font-family: 'Cormorant Garamond', Georgia, serif;
    font-size: 0.45rem;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    font-weight: 600;
    color: rgba(0, 214, 137, 0.7);
}

.date-target-btn.active .date-target-label {
    color: rgba(0, 255, 170, 0.9);
}

.date-target-value {
    font-family: 'Cormorant Garamond', Georgia, serif;
    font-size: 0.65rem;
    font-weight: 600;
    color: rgba(230, 242, 246, 0.9);
    white-space: nowrap;
}

.date-target-value.is-placeholder {
    color: rgba(0, 214, 137, 0.4);
    font-style: italic;
}

.date-target-btn.active .date-target-value {
    color: #00ffaa;
}

/* Calendar Container */
.custom-calendar-card .custom-calendar {
    margin-top: 0;
    padding: 10px;
    border-radius: 10px;
    background: radial-gradient(circle at 0% 0%, rgba(0, 214, 137, 0.25), transparent 60%),
                radial-gradient(circle at 100% 100%, rgba(18, 194, 255, 0.18), transparent 60%),
