    grid-template-columns: repeat(7, 1fr);
    gap: 2px;
}

.custom-calendar-card .calendar-day {
    width: 32px;
    height: 28px;
    border-radius: 6px;
    border: 1px solid transparent;
    background: radial-gradient(circle at 50% 0%, rgba(255, 255, 255, 0.1), transparent 70%),
                rgba(3, 18, 15, 0.8);
    color: rgba(230, 242, 246, 0.85);
    font-family: 'Cormorant Garamond', Georgia, serif;
    font-size: 0.65rem;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.15s ease;
}

.custom-calendar-card .calendar-day:not(.empty):hover {
    background: linear-gradient(135deg, rgba(0, 214, 137, 0.35), rgba(18, 194, 255, 0.25));
    color: rgba(5, 12, 10, 0.98);
    border-color: rgba(0, 214, 137, 0.5);
    box-shadow: 0 0 10px rgba(0, 214, 137, 0.5);
    transform: scale(1.05);
}

.custom-calendar-card .calendar-day.selected {
    background: linear-gradient(135deg, rgba(0, 214, 137, 0.95), rgba(18, 194, 255, 0.8));
    color: rgba(5, 12, 10, 0.98);
    border-color: rgba(0, 255, 170, 0.8);
    box-shadow:
        0 0 0 1px rgba(0, 0, 0, 0.8),
        0 0 16px rgba(0, 214, 137, 0.7);
    font-weight: 700;
}

.custom-calendar-card .calendar-day.in-range {
    background: linear-gradient(135deg, rgba(0, 214, 137, 0.2), rgba(18, 194, 255, 0.18));
    color: rgba(0, 255, 170, 0.95);
